@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.Button {
    border: none;
    font-family: 'Poppins', sans-serif;
    height: 70px;
    width: 100%;
    font-size: 1.5rem;
    color: white;
    font-weight: bold;
    border-radius: 20px;
    margin: 0 10px;
}

.Button:hover {
    cursor: pointer;
}

.Button--primary {
    background-color: #4ABCAD;
}

.Button--secondary {
    background-color: #FC4A1A;
}
.Intro {
    background-color: rgb(28, 28, 39);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Intro__title {
    font-family: 'Lobster', cursive;
    color: white;
    font-size: 7rem;
    margin: 0;
}

.Intro__section {
    display: flex;
    width: 100%;
    max-width: 500px;
}
html, body {
    background-color: rgb(28, 28, 39);
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

#root {
    width: 100%;
    height: 100%;
}

.App {
    width: 100%;
    height: 100%;
}
